                                    d="M0.5 18C0.5 13.3587 2.34374 8.90752 5.62563 5.62563C8.90752 2.34374 13.3587 0.5 18 0.5C22.6413 0.5 27.0925 2.34374 30.3744 5.62563C33.6563 8.90752 35.5 13.3587 35.5 18C35.5 22.6413 33.6563 27.0925 30.3744 30.3744C27.0925 33.6563 22.6413 35.5 18 35.5C13.3587 35.5 8.90752 33.6563 5.62563 30.3744C2.34374 27.0925 0.5 22.6413 0.5 18ZM17.0013 25.49L27.0767 12.8947L25.2567 11.4387L16.6653 22.1743L10.58 17.104L9.08667 18.896L17.0013 25.4923V25.49Z"
                                    fill="#7534FC"
